<template>
    <div>

    <header class="bm-centered">
      <span class="title">L U M I E R<img class="header-logo" src="favicon.ico"/></span>
      <span class="tienda">Tienda Digital - Artculos de electrnica</span>
      <span  class="envio">Envos Gratis - Guernica - Glew - Korn - San Vicente</span>
    </header>

         <ProductsGrid :products="products" @showProductDetail="showProductDetail"/>
    </div>
</template>

<script>
import CONFIG from './../../config.js' //borrar
import ProductsGrid from './products/ProductsGrid.vue'


export default {
  name: 'Shopping',
   components: {
    ProductsGrid,
  },
 created: function () {
    this.getCatalog()   
    setInterval(this.getCatalog, 60000  * CONFIG.CATALOG_MINUTES_TIMEOUT);  
  },
   props: {
     product : Object
  },
  data () {
      return {
        // imagePrefix: CONFIG.IMAGES_PREFIX,
        products: [],
        items: []
         }
    },
  methods: {
    getCatalog: function () {
      fetch(CONFIG.CATALOG, {
                            method: 'get',
                            headers: {'content-type': 'text/csv;charset=UTF-8'} 
            })
      .then(response => response.text())
      .then(v => this.parseCSV(v))
      .catch(err => console.log(err));
    },
    parseCSV: function(input) { 
      const rows = input.split("\r\n");
      const header = rows.shift().split(","); //removing first line
      var productList = [];
     
      rows.forEach(element => {

        let product = {}
        const properties = element.split(",");
    
        for (var i = 0; i < header.length; i++){
          product[header[i]] = properties[i];
        }

        productList.push(product)    
        });

        this.products = productList;
    },
    showProductDetail: function(product){
       this.$emit("showProductDetail", product)
    },
     

  },

}
</script>

