<template>
    <li>
    <div class="uk-card uk-card-hover uk-card-body">
            <p>{{title}} - ${{price}}</p>
            <div class="uk-inline">
              <a @click="showProductDetail()">
            <img :src="imagePrefix + code +'.jpg'" width="120" height="120">
              </a>
            <span class="price uk-position-bottom-right"></span>

            </div>
            
                <div class="uk-margin uk-flex uk-flex-between uk-vertical-align">
                           
                    <span>
                        <select v-model=selectedQuantity @change="quantityChange"  id="quantityInput" class="uk-select uk-form-width-xsmall">
                            <option v-for="n in stock" :key=n :value=n>{{n}}</option>
                        </select>
                    </span>

                    <span >$ {{subtotal}}</span>
                
                </div>

            <a  @click="deleteItemCode"><span  class="uk-position-center-right bm-icon" uk-icon="icon: trash; ratio: 1;"></span></a>
                        
        </div>

    </li>

</template>

<script>
import CONFIG from './../../../config.js'
export default {
  name: 'CartItem',
  props: {
    price: Number, 
    code: String, 
    title: String, 
    quantity: Number,
    stock: Number
            },
    data () {
    return {
      imagePrefix: CONFIG.IMAGES_PREFIX,
      //imagePrefix: "../../../assets/products/",
      selectedQuantity: this.quantity
        }
  },
    methods: {
        quantityChange() {
         let codeAndQuantity = {code:this.code,quantity:this.selectedQuantity}
         this.$emit('quantityChange', codeAndQuantity);
        },
        deleteItemCode(){
          // console.log("delteIemCode" + this.code)
           let item = {code:this.code};
          // console.log(item)
           this.$emit('deleteItem', item);
        },
        showProductDetail: function(){
          let item = {code:this.code, title:this.title, price:this.price, stock:this.stock}
          this.$emit("showProductDetail", item)
       },
    } , 
    computed: {
        subtotal: function() {
        return this.quantity * this.price;
         }
    },
    watch: {
      quantity: {
      handler(value) {
        this.selectedQuantity = value
      }
    }
  }
   
    
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.cart{
    background-color: white;
    width: 80%;
    
}
.bm-icon{
  padding-right:30px;
}
.price{
    /* background:blue; */
    color:grey;
    /* border:rounded; */
}
</style>