<template>
    <div>
        <ul class="uk-list" v-for='item in items' :key='item.code'>
              <CartItem :price=parseInt(item.price) :code="item.code" :title="item.title" :quantity=parseInt(item.quantity) :stock=parseInt(item.stock) @quantityChange="quantityChange" @deleteItem="deleteItem" @showProductDetail="showProductDetail"/>       
        </ul>
    </div>
</template>

<script>
// import CONFIG from './../../../config.js'
import CartItem from './CartItem.vue'
export default {
  name: 'CartList',
  components: {
    CartItem
  },
  props: {items: Array},
     methods: {
      deleteItem: function(item) {
        this.$emit("deleteItem", item);      
       },
       quantityChange: function(quant){
         this.$emit("quantityChange",quant)
       },
       showProductDetail: function(item){
         this.$emit("showProductDetail",item)
       }
      
    },
    // data () {
    //    return {
    //         imagePrefix: CONFIG.IMAGES_PREFIX
    //     }
    // }
    
  
}
</script>

