<template>
  <div>
   <MyCart :cartSize="cartSize" :pageToShow="pageToShow" @showPage="showPage"/>
   <Shopping v-show="pageToShow == 'shop'" @cartSize="updateCartSize"  @showProductDetail="showProductDetail" @showPage="showPage"/>
   <ProductDetail  v-show="pageToShow == 'productDetail'" :product="product" @addToCart="addToCart" @showPage="showPage"/>
   <Cart  v-show="pageToShow == 'cart'"  :items="items" @deleteItem="deleteItem" @showPage="showPage" @quantityChange="quantityChange" @showProductDetail="showProductDetail"/>
   <Footer/>
</div>
</template>

<script>
import MyCart from './layout/MyCart.vue'
import Footer from './layout/Footer.vue'
// import Hero from './layout/Hero.vue'
import Shopping from './shop/Shopping.vue'
import ProductDetail from './shop/products/ProductDetail.vue'
import Cart from './shop/cart/Cart.vue'
import uikit from './../../public/uikit.min.js'
import CONFIG from './../config.js'

export default {
  name: 'Home',
  components: {
    MyCart,
    // Hero,
    Shopping,
    ProductDetail,
    Cart,
    Footer
  },
  data () {
      return {
        cartSize: 0,
        pageToShow: "shop",
        newProduct: {},
        items: [],
        product: {title:"titulo", code:"codigo"}
         }
    },
     computed: {
        item_length: function () {
            return this.items;
        }
    },
    mounted: function () {
        if(this.items.length == 0){
          // console.log("items")
          // console.log(this.items)
          var storedItems = JSON.parse(localStorage.getItem("items"));
          // console.log(storedItems)
           if(storedItems && storedItems.length > 0){
            //  console.log("asigno items desde localStorage")
             this.items = storedItems;
           }
        }
      
    },
     watch: {
        item_length: {
           handler: function () {
             this.$emit("cartSize", this.items.length)
            },
        },
         
      },
      methods: {
     updateCartSize: function(size) {
        this.cartSize = size
       },
       showPage: function (page){
         window.scrollTo(0,0);
         this.pageToShow = page

       },
      showProductDetail: function(product){
       this.showPage("productDetail")
       this.product = product
      },
      quantityChange: function(info){
        // console.log(info)
   
        const theItem = this.items.find(item => {
            return item.code === info.code;
        });

        let index = this.items.indexOf(theItem)
        this.items = this.items.filter(element => element.code !== theItem.code)
        theItem["quantity"] = info.quantity
        this.items.splice(index, 0, theItem)
        localStorage.setItem("items", JSON.stringify(this.items)); //localStorage
      },
      deleteItem: function(item) {
       this.items = this.items.filter(element => element.code !== item.code)
       localStorage.setItem("items", JSON.stringify(this.items)); //localStorage
       this.updateCartSize(this.items.length)
       },
       addToCart: function(product) {
      
        const dupItem = this.items.find(item => {
            return item.code === product.code;
        });

        if (dupItem !== undefined) {   //if item exists already
          
          if(dupItem.quantity !== undefined & dupItem.quantity + 1 > product.stock)     {       //if item quantity exists   
              uikit.notification({
                message: 'No hay suficiente stock para agregar ' + product.title + ' a su compra',
                status: 'warning',
                pos: 'top-right',
                timeout: CONFIG.NOTIFICATIONS_TIMEOUT
               });
            return null;
           }
          //  console.log("update quantity")
            let index = this.items.indexOf(dupItem)
            this.items = this.items.filter(element => element.code !== dupItem.code)
            dupItem["quantity"] = dupItem["quantity"] + 1
            this.items.splice(index, 0, dupItem)
           
          }else{
              product["quantity"] = 1
              this.items.push(product)
              
          }
      
        uikit.notification({
            message: 'Se agreg ' + product.title + ' a su compra',
            status: 'primary',
            pos: 'bottom-right',
            timeout: CONFIG.NOTIFICATIONS_TIMEOUT
        });
        this.updateCartSize(this.items.length)
        localStorage.setItem("items", JSON.stringify(this.items)); //localStorage
        this.showPage("cart")
       },
   
    
    }  
}
</script>

