<template>
    <div class="cart-container bm-margin-top lista">
        <h1 class="bm-centered">Mi Carrito</h1>
        <div v-if="items.length == 0" style="text-align: center">
            Todava no aadi ningn artculo al carrito.
        </div>

        <div  v-if="items.length > 0" >

            <CartList :items="items" @deleteItem="deleteItem" @quantityChange="quantityChange" @showProductDetail="showProductDetail"/>
            <hr>
                <div class="uk-card uk-card-body">
                      <div class="uk-form-controls uk-form-controls-text">
                        <label><input  v-model="checkoutOpt" class="uk-radio" type="radio" name="radio1" v-bind:value="1"> Retiro en Sucursal (10% dto. en efectivo)</label><br>
                        <label><input  v-model="checkoutOpt" class="uk-radio" type="radio" name="radio1" v-bind:value="2"> Retiro en Sucursal (otros medios de pago)</label><br>
                        <label><input  v-model="checkoutOpt" class="uk-radio" type="radio" name="radio1" v-bind:value="3"> Envo - bonificable segn zona</label>
                    </div>
                    <br>
                <input class="uk-input" id="form-stacked-text" type="text" placeholder="Aclaraciones" v-model="clarifications">
                </div>
               
            <hr>

            <div  class="uk-flex uk-flex-around total uk-text-large">
                <span>Total</span>
                <span>${{total}}</span>
                     
            </div>

            <div class="uk-align-center" v-if="items.length > 0" >
                     <a class="" :href=whatsappLink>
                    <button class="uk-button uk-button-primary uk-align-center"><span  class="uk-icon"  uk-icon="icon: whatsapp; ratio: 1;">Relizar Pedido  </span></button>
                </a>
            </div>

        </div>

        <div class="bm-centered">
            <a  @click="$emit('showPage', 'shop')"><span  class="uk-icon link-button" uk-icon="icon: arrow-left; ratio: 1;">Volver a la Tienda</span></a>
        </div>
    </div>
</template>

<script>
import CartList from './CartList.vue'
import CONFIG from './../../../config.js'

export default {
  name: 'Cart',
  props: {
      items: Array
  },
  components: {
      CartList
  },
   data () {
       return {
            checkoutOpt: '.',
            clarifications: '',
        }
    },
  methods: {
     deleteItem: function(item) {
        this.$emit("deleteItem", item);      
       },
    quantityChange: function(quant){
        this.$emit("quantityChange",quant)
    },
    showProductDetail: function(item){
        this.$emit("showProductDetail",item)
    }
  },
  computed: {
      total: function(){
          var value = 0
          for(var i=0;i<this.items.length;i++){
              value = value + this.items[i].price * this.items[i].quantity;
          }
          if (this.checkoutOpt == 1){
            return Math.round(value*0.9)
          }else{
              return value
          }
      },
      message: function(){
          let mess = "Buenas, mi pedido es: "
        //   console.log(this.items)
          if (this.items.length > 0){
              this.items.forEach(function(item){
                mess = mess + item.code + "(" + item.quantity + ") - "
                })
               mess = mess + "Envio: " + this.checkoutOpt + " - "
               mess = mess + "Aclaraciones: " + this.clarifications + " - "
               mess = mess + "Subtotal: " + this.total
          }
          return mess
      },
      whatsappLink: function(){
          return "https://api.whatsapp.com/send/?phone=" + CONFIG.PHONE_NUMBER + "&text=" + this.message + "&app_absent=0"
      }
  },
}
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.cart{
    background-color: white;
    width: 80%;
    
}
.link-button{
    margin-top: 30px;
}
.cart-container{
    max-width: 100%;

}
.total{
    margin-right:20px;
}

/* Mobile */
@media only screen and (max-width: 600px) {

}
/* Desktop */
@media only screen and (min-width: 601px) {
.lista{
      padding-left: 20%;
    padding-right: 20%;
}
}
</style>