<template>
<div>
  <div>
     <a   @click="$emit('showPage', 'shop')"><span  class="uk-icon link-button subtitle" uk-icon="icon: arrow-left; ratio: 1;">Volver a la Tienda</span></a>
  </div>
    <div class="product-description row">

      <div class="column">
  
            
            <div class="uk-align-center">
              
              <div class="uk-card bm-flex prod-card">
                <img :src="imagePrefix + product.code +'.jpg'" :alt="product.title">      
                <img  v-if="product.stock<3 && product.stock>0"  class="uk-position-top-right badge" src="../../../assets/ultimos.png" >
                <img  v-if="product.stock<1"  class="uk-position-top-right badge" src="../../../assets/sinstock.png" >
                
              </div>

             

            </div>

      </div>
       <div class="column">
          
            <!-- <div class="uk-align-center">  -->

              <div class="title" >{{product.title}}</div>
               <div  class="uk-text-large price">$ {{product.price}}</div>

                <button v-if="product.stock>0"  @click="$emit('addToCart', product)" class="uk-button uk-button-primary add-button">
                    <span>Agregar al carrito</span>
                </button>

                <hr>
              <!-- <dt>Descripcion</dt> -->
               <dt class="available">Disponibles: {{product.stock}}</dt>
                <a v-if="product.stock<1" :href=whatsappLink>
                    <button class="uk-button uk-button-primary uk-align-center">
                      <span  class="uk-icon"  uk-icon="icon: whatsapp; ratio: 1;">Consultar </span>
                    </button>
                </a>
                
                <dl v-for='desc in descriptions' :key='desc.code' class="uk-description-list">
                  <dt>{{desc.title}}</dt>
                  <dd>{{desc.text}}</dd>
                </dl>

            <!-- </div> -->
 

          
      </div>
  </div>
  <!-- Back Button -->
  <div class="bm-centered">
     <a   @click="$emit('showPage', 'shop')"><span  class="uk-icon link-button subtitle" uk-icon="icon: arrow-left; ratio: 1;">Volver a la Tienda</span></a>
  </div>
</div>

</template>

<script>
import CONFIG from './../../../config.js'
export default {
  name: 'ProductDetail',
  props: {
     product : Object
  },
  data () {
          return {
              imagePrefix: CONFIG.IMAGES_PREFIX,
              descriptions: []
          }
  },
  methods: {
    parseDescription: function(){

      let sections = this.product.description.split(";")

      this.descriptions = []

      for (let i = 0; i < sections.length; i++){

        const titleDesc = sections[i].split(":");

        let descObject=  {id:i, title:titleDesc[0], text:titleDesc[1]};
    
         this.descriptions.push(descObject) 
        }

        // console.log("description")
        // console.log(this.descriptions)


    },
    
  },
  watch: {
    product: function() {
      // console.log("actualizando description")
      this.parseDescription();
    }
  },
  computed: {
    message: function(){
      return "Buenas, quiero consultar por el producto " + this.product.code
    },
    whatsappLink: function(){
          return "https://api.whatsapp.com/send/?phone=" + CONFIG.PHONE_NUMBER + "&text=" + this.message + "&app_absent=0"
      }
  }
}
  
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.subtitle{
  margin:20px;
}
.product-description{
  padding: 20px;
}
.badge{
  width:150px;
  padding-top:20px;
  padding-right:20px;
}
  .title{
    margin-bottom: 10px;
    font-size: large;
  }

 .add-button{
    margin-bottom: 20px;
  }

  .price{
    margin-bottom: 20px;
  }

.prod-card{
  max-width: fit-content;
  margin: auto;
}
 
 

/* Mobile */
@media only screen and (max-width: 600px) {
img {
  width: 80% ;
  margin: auto;
}
.available{
  /* text-align: right; */
  margin-bottom: 20px;
  color: grey;
}
.add-button{
  /* text-align: right; */
  width: 100%;
}
.price,.title{
  text-align: center;
}
.price{
  font-size: medium;
}
}
/* Desktop */
@media only screen and (min-width: 601px) {
  header{
    display:flex;
    align-items: center;
    justify-content: space-around;
  }
  .margin-bottom-25{
    margin-bottom: 25px;
  }
  img {
    width: 100%;
    max-width: 400px;
    margin: auto;
  }
  .column {
    float: left;
    width: 50%;
  }

  /* Clear floats after the columns */
  .row:after {
    content: "";
    display: table;
    clear: both;
  }

  .title{
    font-size:large;
  }

 
  
}

</style>