<template>
    <!-- <div class="uk-grid-column-small uk-grid-row-large uk-child-width-1-3@s uk-text-center bm-margin-top" uk-grid>      -->
    <div class="uk-grid-column-small uk-grid-row-medium uk-child-width-1-3@s uk-text-center" uk-grid>     
        <div v-for='product in products' :key='product.code' class="product-grid">
         
         <div class="product-title" @click="showProductDetail(product)">
                <span class="uk-text-large">{{product.title}}</span>
                <div class="uk-card uk-card-body prod-card">
                  
                  <img class="catalogImage" :src="imagePrefix + product.code +'.jpg'" :alt="product.title">
                  <img  v-if="product.stock<3 && product.stock>0"  class="uk-position-top-right badge" src="../../../assets/ultimos.png" >
                  <img  v-if="product.stock<1"  class="uk-position-top-right badge" src="../../../assets/sinstock.png" >
                </div>
          </div>
                <div class="uk-card-title price">
                    $ {{product.price}} 
                </div>
                <!-- <button :disabled="product.stock<1"   @click="showProductDetail(product)" class="uk-button uk-button-primary uk-align-center">VER DETALLE</button> -->
                <button @click="showProductDetail(product)" class="uk-button uk-button-primary uk-align-center see-detail">LO QUIERO</button>
          <hr>
        </div>
        
    </div>
</template>

<script>
import CONFIG from './../../../config.js'
export default {
  name: 'ProductsGrid',
  props: {products: Array},
  data () {
          return {
              imagePrefix: CONFIG.IMAGES_PREFIX
          }
  },
  methods: {
      showProductDetail: function(product){
       this.$emit("showProductDetail", product)
    },
  },
  

}
</script>

<style scoped>
.catalogImage {
  width: 250px;
  display: block;
  margin: auto;
  /* padding: 10px; */
}
.price{
  margin-bottom:5px;
}
.badge{
  width:150px;
  padding-top:20px;
  padding-right:20px;
}
.product-grid{
  margin:auto;
}
.product-title{
  cursor:pointer;
  padding-right:10px;
  padding-left:10px;
  margin-top: 25px;
}
.see-detail{
  margin-top:10px;
}
.prod-card{
  padding:15px;
  max-width: fit-content;
  margin: auto;
}

 
</style>
